.home-page {
  text-align: center;
  padding: 40px;
}

.home-page h1 {
  color: #00a651;
  font-size: 36px;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.home-page p {
  color: #555;
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.home-page .services {
  margin-top: 60px;
}

.home-page .services h2 {
  color: #00a651;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.home-page .services ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 40px;
}

.home-page .services li {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 10px;
}

.home-page .about-us {
  margin-top: 60px;
}

.home-page .about-us h2 {
  color: #00a651;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.home-page .about-us p {
  color: #555;
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 40px;
}

.home-page .contact-info {
  margin-top: 60px;
}

.home-page .contact-info h2 {
  color: #00a651;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.home-page .contact-info p {
  color: #555;
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 40px;
}

.home-page .btn {
  display: inline-block;
  margin-top: 60px;
  padding: 10px 20px;
  background-color: #00a651;
  color: #fff;
  text-decoration: none;
  border-radius: 4px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s ease;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.home-page .btn:hover {
  background-color: #008e46;
}


.create-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.create-form label {
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.create-form p {
  margin: 0;
}

.create-form input {
  width: 400px;
  height: 25px;
  border: 0;
  border-radius: 10px;
  background-color: #4e8b6b;
    padding: 5px 10px;
  margin-bottom: 20px;
}

.create-form button {
  width: 400px;
  height: 23px;
  border: 0;
  border-radius: 10px;
  background-color: #708090;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-form button:hover {
  background-color: #555;
}
